import React, { useState, useEffect } from "react";
import myStyle from './Report.module.scss';


function Report({imageSrc}) {
  const [imageLoaded, setImageLoaded] = useState(false); //   

  useEffect(() => {
    //    
    const img = new Image();
    img.src = imageSrc;
    img.onload = () => setImageLoaded(true); //      
  }, []);

  return (
    <div className={myStyle.Report}>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        {imageLoaded ? (
          <img
            src={imageSrc}
            className={myStyle.imgSrc}
            // style={{
            //   width: '90%', //      
            //   height: 'auto', //  div  
            //   objectFit: 'cover',
            //   position: 'relative', //   
            //   top: '-1em', //   (  )
            
            //   maskImage:
            //   'linear-gradient(to top, rgba(150, 255, 150, 0.15) 10%, rgba(255, 255, 255, 1) 60%)',
            //   maskRepeat: 'no-repeat',
            // }}
            alt="Example"
          />

        ) : (
          <div style={{ width: '70%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <p></p>
          </div>
        )}
        
      </div>

      <div className={myStyle.textContent}>
           ,  .
        <br />      .
        <br />      ,  .
        <br />
        <br />
        <span className={myStyle.mainContent}>
              <span className={myStyle.point}></span>  .
        </span>
      </div>
      
    </div>
  );
}

export default Report;
