import React, { useEffect , useState , useRef } from "react";
import myStyle from "./Directions.module.scss";
import KakaoMap from "./KakaoMap";
import MapLinks from "./MapLinks";
import { KakaoContext } from "./KakaoMapLoader";

function Directions() {

  const latitude = 36.593777; // 
  const longitude = 127.486195; // 
  const placeName = " "; //  

  return (
    <div className={myStyle.container}>
      <div className={myStyle.title}> 
        <span style={{fontSize:"1.6em" , lineHeight:"1.6em"}}>
             <br/>
        </span>
        <span style={{fontSize:"1.05em" ,color:"rgba(0,0,0,0.7)"}}>
              349
        </span>
      </div>

      <div className={myStyle.main}> 

        <div className={myStyle.mapView}> 
          <div className={myStyle.map}> 
            <KakaoMap latitude={latitude} longitude={longitude} placeName={placeName}/>
          </div>
          <div className={myStyle.mapIcon}> 
            <MapLinks latitude={latitude} longitude={longitude} placeName={placeName} />
          </div>
        </div>

      </div>


      {/* <img src={calendarImage} alt=" " /> */}
        {/* <div className="Directions">
          <div id="map" style={{ width: "100%", height: "300px" }}></div>
          <div className="map-links">
            <a href="https://map.kakao.com/" target="_blank" rel="noopener noreferrer"></a>
            <a href="https://tmap.co.kr/" target="_blank" rel="noopener noreferrer">T</a>
            <a href="https://map.naver.com/" target="_blank" rel="noopener noreferrer"></a>
          </div> 
        </div> */}
    </div>



  );
}

export default Directions;
